.gallery {
  padding: 58px 0;
  background: linear-gradient(270.76deg, #E3E7F6 -55.5%, rgba(227, 231, 246, 0) 145.36%);

  &__content {
    height: 1228px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3 1fr);
    grid-column-gap: 30px;

  }

  &__descr {
    margin-top: 98px;
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;
    max-width: 109px;
    font-weight: 700;
    font-size: 44px;
    line-height: 62px;
    color: var(--title-color);
  }

  &__text {
    margin: 0;
    max-width: 323px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: var(--text-dark-color);
  }

  &__item {
    overflow: hidden;
    border-radius: 8px;

    &:nth-child(2) {
      max-height: 420px;
    }

    &:nth-child(3) {
      margin-top: 50px;
      max-height: 362px;
    }

    &:nth-child(4) {
      margin-top: 50px;
      max-height: 414px;
    }

    &:nth-child(5) {
      margin-top: auto;
      max-height: 340px;
    }
    &:nth-child(6) {
      margin-top: auto;
      max-height: 275px;
    }
  }
}
